import React from 'react';
import {Grid} from '@material-ui/core';
import TextPan1 from '../components/text-pan1';
import HistoryItem from './history-item';
import {connect} from 'react-redux';
import Axios from 'axios';
import eventBus from "../EventBus";

class HistoryPanel extends React.Component {
    constructor(props) {
        super(props);
        this.clr = props.borderColor;
        this.which = props.which;
        // console.log(props.data);
    }
    render() {
        return (
            <div>
                <div style={{position:'relative', top:"-4%"}}>
                    <TextPan1 text={"The payment history of "+this.which+" team"}  color="#5aeaf3" borderColor = {this.clr} backColor="#0a1f42" fontSize="16px" flt="none"/>
                </div>
                <div className="history-card" style={{borderColor:this.clr, color:this.clr, textAlign:"center"}}>
                    <Grid container spacing={1} className="diceGrid" justify="center">
                    {
                        this.props.data && this.props.data.length>0 &&
                        this.props.data.map(elem=>{
                            return <Grid item xs={12} sm={12} md={12} >
                                        <HistoryItem data={elem} key={elem.id}/>
                                    </Grid>
                        })
                    }
                    </Grid>
                </div>
            </div>
        )
    }
}

// const mapStateToProps = state => {
