import React from 'react';
import {Grid} from '@material-ui/core';

class HistoryItem extends React.Component  {

    render() {
        let sum=parseFloat(this.props.data.amount)+parseFloat(this.props.data.fee);
        return (
            <a href={this.props.data.trans_address} target="_blank">
                <div className="history-item">
                        {sum} ETH from {this.props.data.wallet_key}
                        = {this.props.data.amount} + fee {this.props.data.fee}
                        = 61.2 % share | status: 0.00623247 ETH payout
                </div>
            </a>
        )
    }
}
export default HistoryItem;