import React from 'react';
import TextPan1 from '../components/text-pan1';

import urltext from '../assets/img/url-text1.png';
import logo from '../assets/img/logo2.png';
import beltOfVikings from '../assets/img/beltofvikings.png';


export default class Navigation extends React.Component{

  constructor(props) {
    super(props);
    this.state = {
        screenWidth: window.innerWidth
    };
  }
  componentDidMount() {
    this.setState({screenWidth: window.innerWidth});
  }
  render(){
    return(
      <div>
        <div>
          <div align = "left" className="diceGrid1" >
            <div className = "mt-2 mb-2" style = {this.state.screenWidth<800?{marginLeft: "5%"}:null}>
              <img src = {urltext} alt = 'logo' width = "250px"/>
            </div>
            <div className = "x-font-big-white" style = {this.state.screenWidth<800?{marginLeft: "5%",padding:"2px"}:null}>
              <span style={{paddingBottom:"10px"}}>Simple, provably fair ethereum game.   No registration required.   <a href = "google.com" style={{color:"white"}} >How does it work?</a></span>
            </div>
          </div>
          <div className = "x-nav-line-bar">
            <div style={{marginLeft:"20%", paddingTop:"2px"}} >
              {/* <TextPan1 text = "Previous Round" color="#5aeaf3" borderColor = "#5aeaf3" backColor="#0a1f42" link="#trrr" fontSize="16px"/> */}
              <TextPan1 text = {`Current Round # ${this.props.curRound}`} color="#5aeaf3" borderColor = "#5aeaf3" backColor="#0a1f42" ml="3%" dw={this.state.screenWidth}  fontSize="16px"/>
              <span className="nav-time-cls" style = {this.state.screenWidth<800?{display: "none"}:null}>
                | This round ends in 
                <span style={{color:"#a2f6fb", marginLeft:"5px"}}>{this.props.leftTime}</span>
              </span>
            </div>
          </div>
        </div> 
        <span className="fish-logo" style = {this.state.screenWidth<800?{marginLeft: "-10%"}:null}>
          <img src = {logo} alt = 'logo' width = "130px"/>
        </span>
        <span className = "ffish-logo" style = {this.state.screenWidth<800?{display: "none"}:null}>
          <img src = {beltOfVikings} alt = "beltOfVikings" width = "60%"/>
        </span>
      </div>
    );
  }
}