import React from 'react';
import {Grid} from '@material-ui/core';
import Navigation from '../components/custom-header1';
import Card from '../components/Card';
import PaymentHistory from './pay-history';
import AdminSettingDlg from '../components/admin_settings';
import AdminPageCmp from '../components/admin_page';
import CustomizedProgressBars from '../components/gameover_alarm';
import Abi from '../assets/abi/abi.json';

import Web3 from 'web3';
import moment from 'moment-timezone';
import Axios from 'axios';
import eventBus from "../EventBus";
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import  { ethers } from 'ethers';

import goldfinch from '../assets/img/goldfinch.png';
import redauth from '../assets/img/red-auth.png';
import greenauth from '../assets/img/green-auth.png';

import slide1 from '../assets/img/png/game_stop.PNG';
import slide2 from '../assets/img/png/game_start.PNG';
import slide3 from '../assets/img/png/a_won.PNG';
import slide4 from '../assets/img/png/b_won.PNG';
import slide5 from '../assets/img/png/a_winning.PNG';
import slide6 from '../assets/img/png/b_winning.PNG';

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

class Dashboard extends React.Component {
    static gst1;
    static gst2;
    static gst;
    constructor(props) {
        super(props);

        this.state = {
            roundSettings:{
                round_number: 0,
                jackpot: 0.005,
                min_fee: 1,
                max_fee: 50,
                min_bet: 0.001,
                max_bet: 10,
                round_times: 8
            },
            timestr: "00:00:00", 
            curRound: 0,
            curFee: 0,
            curStatusIndex: 0,
            publicKey: "Connect",
            walletKey:"",
            diffValue:0.0,
            payValue:0.0,
            startBeforeCounter:60,
            matching:true,
            alarmText:"The match can be not started now",
            admin_setting_open:false,
            gameover_alarm_open:false,
            seconds: 28800
        };

        this.timer = 0;
        this.monitorTimer = 0;
        this.resItem = [slide1, slide2, slide3, slide4, slide5, slide6];
        this.screenWidth = window.innerWidth;
        this.timeflag=true;
        this.connectMetamask = this.connectMetamask.bind(this);
        this.start = this.start.bind(this);
        this.loadRoundSettings = this.loadRoundSettings.bind(this);
        this.startTimer=this.startTimer.bind(this);
        this.timerEvent=this.timerEvent.bind(this);
        this.startMonitorTimer=this.startMonitorTimer.bind(this);
        this.timerMonitorEvent=this.timerMonitorEvent.bind(this);
        this.loadWalletKey();
        this.createCollection();

        let df=this.diffBetweenTimezone(moment.tz.guess(),"Europe/Minsk");
        Dashboard.gst1 = ( 24 + 6 + df["hour"] ) % 24;
        Dashboard.gst2 = ( 24 + 18 + df["hour"] ) % 24;
    }
    diffBetweenTimezone(zone1, zone2) {
        let now = moment.utc();
        let z1 = moment.tz.zone(zone1).offset(now); 
        let z2 = moment.tz.zone(zone2).offset(now);
        let ds = (z1 - z2) * 60;
        let dm = (z1 - z2);
        let dh = (z1 - z2) / 60;
        return {
            "second":ds,
            "minute":dm,
            "hour":dh
        }
    }
    convertToDate(timestamp) {
        return moment.tz(timestamp, 'X', moment.tz.guess()).format('YYYY-MM-DD HH:mm:ss');
    }
    createAdminCollection() {
        Axios({
            method: "POST",
            url: "http://157.90.69.185:5000/api/referral/create-admin-collection"
        });
    }
    createCollection() {
        Axios({
            method: "POST",
            url: "http://157.90.69.185:5000/api/referral/create-collection",
            data:{
                amount: 0,
                fee: 0,
                total_red_amount: 0,
                total_green_amount: 0,
                wallet_key: "",
                round_number: 0,
                which: "",
                trans_address: ""
            }
        });
    }
    loadWalletKey() {
        let wkey = localStorage.getItem("walletKey");
        if(wkey==null || wkey.length==0) return;
        this.setState({
            walletKey: wkey
        });
    }
    secondsToTime(secs) {
        let hours = Math.floor(secs / (60 * 60));
  
        let divisor_for_minutes = secs % (60 * 60);
        let minutes = Math.floor(divisor_for_minutes / 60);
  
        let divisor_for_seconds = divisor_for_minutes % 60;
        let seconds = Math.ceil(divisor_for_seconds);
  
        let obj = {
            "h": hours,
            "m": minutes,
            "s": seconds
        };
        return obj;
    }
    getTimestamp(curHour) {
        let hstr="";
        if (curHour >= 0 && curHour <= 9) hstr = "0" + curHour;
        else hstr = String(curHour);
        let new_date;
        new_date = moment().format("YYYY-MM-DD");
        let dateStr = new_date+" "+hstr+":00:00";
        return parseInt(moment.tz(dateStr, moment.tz.guess()).format('X'));
    }
    componentDidMount() {
        let web3 = new Web3(window.ethereum);
        web3.eth.getAccounts((err, accounts)=>{
            if (err != null) console.log("An error occurred");
            else if (accounts.length == 0) console.log("User is not logged in to MetaMask");
            else {
                this.setWalletKey(web3);
                this.setRoundID();
            }
        });
        this.start();
        eventBus.on("compare_amount", (data) =>
            this.evolStatus(data)
        );
        eventBus.on("load_round_setting", () =>
            this.loadRoundSettings()
        );
    }
    async loadRoundSettings() {
        // Axios({
        //     method: "POST",
        //     url: "http://157.90.69.185:5000/api/referral/load-admin-collection"
        // }).then((res)=>{
        //     if( res.data && res.data.length > 0 ){
        //         this.setState({
        //             roundSettings: res.data,
        //             seconds: res.data.round_times*3600
        //         });
        //     }
        //     this.selectStartTime();
        //     if(Dashboard.gst >= 0){
        //         this.screenWidth = window.innerWidth;
        //         let now = parseInt(new Date().getTime()/1000);
        //         let st = this.getTimestamp(Dashboard.gst);
        //         let secs = this.state.roundSettings.round_times * 3600 - now + st;
        //         if(secs<0) {
        //             this.setState({matching:false});
        //         }else{
        //             let timeLeftVar = this.secondsToTime(secs);
        //             this.setState({ 
        //                 timestr: this.getTimeStr(timeLeftVar),
        //                 curRound: 0,
        //                 seconds: secs
        //             });    
        //         }
        //     }
        // });
        let rid = await window.$myContract.methods.findOpenedId().call();
        let stData = await window.$myContract.methods.getGeneralInfo(rid).call();
        let rdata = stData.split("#");
        let data = {
            round_number: parseInt(rid),
            jackpot: parseFloat(parseInt(rdata[0])/1e18),
            min_fee: parseInt(rdata[1]),
            max_fee: parseInt(rdata[2]),
            min_bet: parseFloat(parseInt(rdata[3])/1e18),
            max_bet: parseFloat(parseInt(rdata[4])/1e18),
            round_times: parseInt(rdata[5])
        }
        this.setState({roundSettings:data});
        // console.log(this.state.roundSettings);
        this.selectStartTime();
        if(Dashboard.gst >= 0){
            this.screenWidth = window.innerWidth;
            let now = parseInt(new Date().getTime()/1000);
            let st = this.getTimestamp(Dashboard.gst);
            let secs = this.state.roundSettings.round_times * 3600 - now + st;
            if(secs<0) {
                this.setState({matching:false});
            }else{
                let timeLeftVar = this.secondsToTime(secs);
                this.setState({ 
                    timestr: this.getTimeStr(timeLeftVar),
                    curRound: 0,
                    seconds: secs
                });
            }
        }
    }
    onClose = () => {
        this.setState({ admin_setting_open:false });
    }
    onAlarmClose = () => {
        this.setState({ gameover_alarm_open:false });
    }
    evolStatus(data){
        this.setState({
            curStatusIndex: data.data.curIndex,
        })
    }
    componentWillUnmount() {
        eventBus.remove("compare_amount");
    }
    async requestNewJackpot(){
        if(!window.ethereum || !window.ethereum.selectedAddress){
            alert("ethereum or wallet address error");return;
        }
        let rid = await window.$myContract.methods.findOpenedId().call();
        let gasLimitHex = window.$web3.utils.toHex(300000);
        let status = this.state.roundSettings;
        let data = await window.$myContract.methods.setJackpot(Number(rid), ethers.utils.parseUnits(status.jackpot.toString())).encodeABI();
        const chainId = await window.ethereum.request({ method: 'eth_chainId' });
        var txdetail = {
            from: window.ethereum.selectedAddress,
            to: window.$bettingContractAddress,
            value: window.$web3.utils.toHex(window.$web3.utils.toWei(status.jackpot.toString())),
            gas: gasLimitHex,
            gasPrice: window.$web3.utils.toHex(window.$web3.utils.toWei('10', 'gwei')),
            chainId:chainId,
            data:data
        }
        window.ethereum.request({ method: 'eth_sendTransaction', params: [txdetail] }).then( async (res) => {});
    }
    async requestNewRound(){
        if(!window.ethereum || !window.ethereum.selectedAddress){
            alert("ethereum or wallet address error");return;
        }
        let gasLimitHex = window.$web3.utils.toHex(300000);
        let status = this.state.roundSettings;
        let data = window.$myContract.methods.addBet(status.round_times,status.min_fee, status.max_fee, 
                                            ethers.utils.parseUnits(status.min_bet.toString()), 
                                            ethers.utils.parseUnits(status.max_bet.toString())).encodeABI();
        const chainId = await window.ethereum.request({ method: 'eth_chainId' });
        var txdetail = {
            from: window.ethereum.selectedAddress,
            to: window.$bettingContractAddress,
            value: window.$web3.utils.toHex(window.$web3.utils.toWei("0")),
            gas: gasLimitHex,
            gasPrice: window.$web3.utils.toHex(window.$web3.utils.toWei('10', 'gwei')),
            chainId:chainId,
            data:data
        }
        window.ethereum.request({ method: 'eth_sendTransaction', params: [txdetail] }).then( async (res) => {
            // if(res) this.requestNewJackpat();
        });
    }
    async setRoundID() {
        let rid = await window.$myContract.methods.findOpenedId().call();
        var someProperty = {...this.state.roundSettings}
        someProperty.round_number = rid+1;
        this.setState({someProperty});
    }
    setWalletKey(web3) {
        let returnStr = web3.currentProvider.selectedAddress;
        let temp_public_key = returnStr;
        let key_short = temp_public_key.slice(0, 6);
        this.setState({publicKey: key_short+"...", walletKey:returnStr});
        localStorage.setItem("walletKey",returnStr);
    }
    walletKey() {
        if(!window.ethereum)return null;
        let web3 = new Web3(window.ethereum);
        return web3.currentProvider.selectedAddress;
    }
    async checkAdmin() {
        if (window.ethereum) {
            let web3 = new Web3(new Web3.providers.HttpProvider("https://ropsten.infura.io/v3/a590b66fcf6b4daf90cb8214d7e05d20"));
            let myContract = new web3.eth.Contract(Abi, window.$bettingContractAddress);            
            let data = await myContract.methods.getOwner().call();
            data = String(data).toLowerCase();
            let walletAddr = String(this.walletKey()).toLowerCase();
            if(walletAddr.length==0) return;
            if(data === walletAddr){
                this.setState({
                    admin_setting_open: true
                });
            }
        }
    }
    connectMetamask = () => {
        if(this.state.type === "none") return;
        this.setState({clickConnectButton: true});
        if (window.ethereum) {
            let web3 = new Web3(window.ethereum);
            try {
                window.ethereum.enable().then((res)=> {
                    this.setWalletKey(web3);
                    this.checkAdmin();
                    // this.setState({admin_setting_open: true});
                });
            } catch(e) {
            }
        }
    }
    isInRange(startH, hours, curH) {
        let endH = startH + hours;
        let res = false;
        if(endH > 24){
            if(curH >= startH && curH <= 23) res = true;
            else if(curH <= endH-24) res = true;
            else res = false;
        }else if(curH >= startH && curH <= endH) res = true;
        else res = false;
        return res;
    }
    selectStartTime() {
        let nowH = parseInt(moment().format('HH'));
        Dashboard.gst=-1;
        if(this.isInRange(Dashboard.gst1, 8, nowH)) Dashboard.gst=Dashboard.gst1;
        if(this.isInRange(Dashboard.gst2, 8, nowH)) Dashboard.gst=Dashboard.gst2;
    }
    start() {
        let nowH = parseInt(moment().format('HH'));
        let start1=Dashboard.gst1;
        let start2=Dashboard.gst2;
        if(this.isInRange(start1, 8, nowH) || this.isInRange(start2, 8, nowH)){
            this.setState({matching:true});
            setTimeout(() => {
                this.startTimer();
            }, 1000);
        }else{
            this.setState({matching:false});
            this.startMonitorTimer();
        }
    }
    startTimer() {
        if (this.state.matching && this.timer == 0 && this.state.seconds > 0) {
            this.timer = setInterval(this.timerEvent, 1000);
            this.loadRoundSettings();
        }
    }
    startMonitorTimer() {
        if ( this.state.matching && this.monitorTimer==0){
            this.monitorTimer = setInterval(this.timerMonitorEvent, 1000);
        }
    }
    getTimeStr(time) {
        return `${time.h} h: ${time.m} m: ${time.s} s`;
    }
    async processPayment() {
        let winId = await window.$myContract.methods.decideMatch(this.state.roundSettings.round_number).call();
        let data = await window.$myContract.methods.payout(this.state.roundSettings.round_number, winId).encodeABI();
        const chainId = await window.ethereum.request({ method: 'eth_chainId' });
        let gasLimitHex = window.$web3.utils.toHex(300000);
        var txdetail = {
            from: window.ethereum.selectedAddress,
            to: window.$bettingContractAddress,
            value: window.$web3.utils.toHex(window.$web3.utils.toWei("0")),
            gas: gasLimitHex,
            gasPrice: window.$web3.utils.toHex(window.$web3.utils.toWei('10', 'gwei')),
            chainId: chainId,
            data: data
        }
        window.ethereum.request({ method: 'eth_sendTransaction', params: [txdetail] }).then( async (res) => {
            if(res){
                this.setState({gameover_alarm_open:false});    
            }
        });
    }
    callPayout(){
        this.processPayment();
    }
    async closeRound(){
        let rid = await window.$myContract.methods.findOpenedId().call();
        let data = window.$myContract.methods.closeBet(rid).encodeABI();
        const chainId = await window.ethereum.request({ method: 'eth_chainId' });
        let gasLimitHex = window.$web3.utils.toHex(300000);
        var txdetail = {
            from: window.ethereum.selectedAddress,
            to: window.$bettingContractAddress,
            value: window.$web3.utils.toHex(window.$web3.utils.toWei("0")),
            gas: gasLimitHex,
            gasPrice: window.$web3.utils.toHex(window.$web3.utils.toWei('10', 'gwei')),
            chainId: chainId,
            data: data
        }
        window.ethereum.request({ method: 'eth_sendTransaction', params: [txdetail] }).then( async (res) => {
            if(res) {                
                setTimeout(() => {
                    this.callPayout();
                }, 5000);
            }
        });
    }
    async settingStart() {
        let rid = await window.$myContract.methods.findOpenedId().call();
        let rounds = await window.$myContract.methods.getBetId().call();
        if(rid==rounds) {
            this.requestNewRound();
            setTimeout(() => {
                this.requestNewJackpot();
            }, 3000);
        }
        clearInterval(this.monitorTimer);
        this.setState({matching:true});
    }
    timerMonitorEvent() {
        let now = moment().format('HH:mm:ss').split(":");
        let ch = parseInt(now[0]);
        let cm = parseInt(now[1]);
        let cs = parseInt(now[2]);
        let start1 = Dashboard.gst1;
        let start2 = Dashboard.gst2;
        let ph1 = (24 + start1 - 1) % 24;
        let ph2 = (24 + start2 - 1) % 24;
        if(ch == start1 || ch == start2){
            this.settingStart();
            setTimeout(() => {
                this.startTimer();
            }, 2000);
        } else if(ch == ph1 || ch == ph2){
            if( this.timeflag && cm == 59){
                this.setState({alarmText:"Please ready! Match will start soon."});
                this.timeflag = false;
            }
        }
        if(!this.timeflag)
            this.setState({startBeforeCounter:60-cs});
    }
    timerEvent() {
        let seconds = this.state.seconds - 1;
        let totTime = this.state.roundSettings.round_times * 3600;
        let maxFee = this.state.roundSettings.max_fee;
        this.setState({
            timestr: this.getTimeStr(this.secondsToTime(seconds)),
            seconds: seconds,
            curFee: (maxFee-1)*(totTime-seconds+totTime/maxFee)/totTime
        });
        localStorage.setItem("current_fee", this.state.curFee);
        localStorage.setItem("past_second", this.state.seconds);
        localStorage.setItem("current_round", this.state.curRound);
        localStorage.setItem("status", 1);
        if (seconds === 0) {
            clearInterval(this.timer);
            this.setState({gameover_alarm_open:true});
            this.closeRound();
        }
    }
    render() {
        return (
            <div className="dashboard">
                <Dialog
                    open = {!this.state.matching}
                    TransitionComponent={Transition}
                    keepMounted
                    aria-labelledby="alert-dialog-slide-title"
                    aria-describedby="alert-dialog-slide-description"
                >
                    <DialogTitle id="alert-dialog-slide-title">{this.state.alarmText}</DialogTitle>
                    <DialogContent>
                        <DialogContentText id="alert-dialog-slide-description">
                            The match can be started at {Dashboard.gst1}:00:00 or {Dashboard.gst2}:00:00 in your timezone for {this.state.roundSettings.round_times} hours of every day.
                        </DialogContentText>
                        <DialogContentText style={{textAlign:"center", fontSize:"45px"}}>{this.state.startBeforeCounter}</DialogContentText>
                    </DialogContent>
                </Dialog>
                <Navigation leftTime={this.state.timestr} curRound={this.state.roundSettings.round_number+1}/>
                <div className="bodyEffect">
                    <Grid container justify="center">
                        <Grid item xs = {12} sm = {12} md = {6}>
                            <div style={{textAlign:'center', color:"white", paddingTop:"20px"}}>
                                The law of the nature is simple. The big team (the one with more ethers) eats the small one and all its ethers plus the jackpot.
                                At the end of the round these ethers are shared by all players who invested into the big team.
                            </div>
                        </Grid>
                    </Grid>
                    <div className="choose" style = {this.screenWidth<800?{marginLeft: "15%"}:null}>
                        <img src={goldfinch} width="5%" alt="Goldfinch"/>
                        <span style={{fontSize:"25px", marginLeft:"10px"}}>Which side do you want to bet on?</span>
                    </div>
                    <div style={{marginTop:"1%", marginLeft:"12%"}}>
                        <img src={this.resItem[this.state.curStatusIndex]} width="90%"/>
                    </div>
                    <div style={{padding:"3%", marginRight:"5%"}}>
                        <Grid container spacing = {3} className = "text-center">
                            <Grid item xs = {12} sm = {12} md = {6}>
                                <Card   type="red" cardCls="red_card" cardColor="red" recText="The red team received:" recEthAmount="0.00629499 ETH" 
                                        recComment="0.00629499 ETH (incl. unconfirmed)"  walletKey={this.state.walletKey}
                                        requirement={this.state.roundSettings.min_bet}
                                        authorImg={redauth} addrText="1Mon8DByNfLRhnN1M2wTs6iDhT6K7abS4K" btnCls="cont_red_btn"  inputCls="betrow-red-input"
                                        fee={this.state.curFee} round={this.state.curRound} minBet={this.state.roundSettings.min_bet} maxBet={this.state.roundSettings.max_bet}
                                />
                            </Grid>
                            <Grid item xs = {12} sm = {12} md = {6}>
                                <Card   type="green" cardCls="green_card" cardColor="green" recText="The green team received:" recEthAmount="0.00088541 ETH" 
                                        recComment="0.00088541 ETH (incl. unconfirmed)"  walletKey={this.state.walletKey}
                                        requirement={this.state.roundSettings.min_bet}
                                        authorImg={greenauth} addrText="1GmQ9K4mEXmYeygYrAGndmaDjRGxLnQBrK"  btnCls="cont_green_btn" inputCls="betrow-green-input"
                                        fee={this.state.curFee} round={this.state.curRound} minBet={this.state.roundSettings.min_bet} maxBet={this.state.roundSettings.max_bet}
                                />
                            </Grid>
                        </Grid>
                    </div>
                    <div style={{height:"fit-content", backgroundColor:"#0c132c"}} >
                        <div className="divJackpot" style={{fontSize:"18px"}}>
                            <div><span style={{fontSize:"20px"}}>This round's jackpot:</span><div className="jackpotCls" style={{fontSize:"20px"}}>{this.state.roundSettings.jackpot} ETH</div></div>
                            <div>This jackpot will be added to ethers shared by winning team at the end of the round.</div>
                        </div>
                        <div style={{ height:"150px", marginTop:"50px", textAlign:"center", color:"white", backgroundColor:"#0b2146", wordWrap:"break-word"}}>
                            <div><span style={{fontSize:"18px"}}>Current fee:</span><div className="jackpotCls" style={{fontSize:"20px"}}>{this.state.curFee.toFixed(4)}%</div></div>
                            <div style={{wordWrap:"break-word"}}>This is what we subtract from your payment  it means that 73.961 % will go to the team of your choice.{this.screenWidth>800?<br/>:""}
                                The fee is slowly raising during this round, it starts at {this.state.roundSettings.min_fee} % and ends at {this.state.roundSettings.max_fee} % rate.{this.screenWidth>800?<br/>:""}
                                Calculate carefully but act quickly! Or perhaps you'd better wait for the next round...{this.screenWidth>800?<br/>:""}
                            </div>
                        </div>
                        <PaymentHistory/>
                    </div>
                </div>
                <div className="pos_of_connect" style = {this.screenWidth<800?{marginTop: "-10%"}:null}>
                    <button className="cont_connect_btn" onClick={this.connectMetamask}>
                        <span style={{wordWrap: "break-word"}}>{this.state.publicKey}</span>
                    </button>
                </div>
                <AdminPageCmp open={this.state.admin_setting_open} onClose={this.onClose}/>
                <CustomizedProgressBars open={this.state.gameover_alarm_open} onClose={this.onAlarmClose}/>
            </div>
        )
    }
}
  
export default Dashboard;
