import React from 'react';

class HelpNote extends React.Component {
    render () {
        const {payValue, diffValue, fee, display} = this.props;
        let feeValue = payValue*fee/100;
        return (
            <div className="help_note note_anim" style={(display=="none")?{display:"none"}:null}>
                <h5>Help!</h5><br/> I need at least this payment to survive: 
                <h5>{Number(payValue).toFixed(8).toString()} ETH </h5> from you
                = {Number(diffValue).toFixed(8).toString()} + fee {Number(feeValue).toFixed(8).toString()} ({Number(fee).toFixed(2)} %)
                = 100.0 % share | status: 0.00317559 ETH payout
                (or multiple small payments)
            </div>
        )
    }
}
export default HelpNote;
