import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';

import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Button from '@material-ui/core/Button';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Grid from '@material-ui/core/Grid';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Axios from 'axios';
import eventBus from "../EventBus";
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles((theme) =>
    createStyles({
        formControl: {
            margin: theme.spacing(1),
            minWidth: 120,
        },
        selectEmpty: {
            marginTop: theme.spacing(2),
        },
    }),
);

export default function AdminSettingDlg(props) {
    const handleClose = () => {
        props.onClose();
    };

    const loadData = () => {
        loadRoundSettings();
    };

    const checkRoundSetting = () => {
        if(roundNumber==0 || roundTimes==0 || minFee==0 || maxFee==0
        || minBet==0 || maxBet==0 || jackpot==0 ) return false;
        return true;
    }

    const updateRoundSetting = () => {
        if(!checkRoundSetting()){
            alert("please type the correct informations");return;
        }
        let mydata={
            round_number: roundNumber,
            jackpot: jackpot,
            min_fee: minFee,
            max_fee: maxFee,
            min_bet: minBet,
            max_bet: maxBet,
            round_times: roundTimes,
            process_mode: state.checkedB
        }
        Axios({
            method: "POST",
            url: "http://157.90.69.185:5000/api/referral/update-admin-collection",
            data:mydata
        }).then((res)=>{
            if(res) {
                eventBus.dispatch("load_round_setting", {});
                alert("save successfully");
                handleClose();
            }
        });
    };
    
    const loadRoundSettings = () => {
        Axios({
            method: "POST",
            url: "http://157.90.69.185:5000/api/referral/load_round_settings"
        }).then((res)=>{
            if(res){
                setRoundNumber(res.data.round_number);
                setRoundTimes(res.data.round_times);
                setMinFee(res.data.min_fee);
                setMaxFee(res.data.max_fee);
                setMinBet(res.data.min_bet);
                setMaxBet(res.data.max_bet);
                setJackpot(res.data.jackpot);
                setDisibledCmp(res.data.process_mode);
            }
        });
    }

    const saveMatchProfile = () => {
        props.onClose();
    };

    const [roundNumber, setRoundNumber] = React.useState(0);
    const [roundTimes, setRoundTimes] = React.useState(0);
    const [minFee, setMinFee] = React.useState(0);
    const [maxFee, setMaxFee] = React.useState(0);
    const [minBet, setMinBet] = React.useState(0);
    const [maxBet, setMaxBet] = React.useState(0);
    const [jackpot, setJackpot] = React.useState(0);
    const [mode, setMode] = React.useState('');
    const [disibledCmp, setDisibledCmp] = React.useState(true);

    const handleChange = (event) => {
        if(event.target.id=="round_number")
            setRoundNumber(event.target.value);
        else if(event.target.id=="min_fee")
            setMinFee(event.target.value);
        else if(event.target.id=="demo-simple-select"){
            console.log(event.target.value);
            setRoundTimes(event.target.value);
        }else if(event.target.id=="max_fee")
            setMaxFee(event.target.value);
        else if(event.target.id=="min_bet")
            setMinBet(event.target.value);
        else if(event.target.id=="max_bet")
            setMaxBet(event.target.value);
        else if(event.target.id=="jackpot")
            setJackpot(event.target.value);
    };

    const handleSelectChange = (event) => {
        setRoundTimes(event.target.value);
    };

    const handleSwitchChange = (event) => {
        setState({ ...state, [event.target.name]: event.target.checked });
        setDisibledCmp(!state.checkedB);
    };

    const [state, setState] = React.useState({
        checkedA: true,
        checkedB: true,
    });

    const classes = useStyles();
    // loadRoundSettings();

    return (
        <Dialog
            open = {props.open}
            onClose = {handleClose}
            TransitionComponent = {Transition}
            keepMounted
            aria-labelledby="alert-dialog-slide-title"
            aria-describedby="alert-dialog-slide-description"
        >
            <DialogTitle id="alert-dialog-slide-title">Administrator's Settings</DialogTitle>
            <DialogContent>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <FormControl disabled={disibledCmp}>
                            <InputLabel htmlFor="component-simple">Round Number</InputLabel>
                            <Input id="round_number" value={roundNumber} onChange={handleChange}/>
                        </FormControl>
                        <FormControl disabled={disibledCmp}>
                            <InputLabel htmlFor="component-simple">Jackpot</InputLabel>
                            <Input id="jackpot" value={jackpot} onChange={handleChange}/>
                        </FormControl>
                        <FormControl disabled={disibledCmp}>
                            <InputLabel htmlFor="component-helper">min fee</InputLabel>
                            <Input id="min_fee" value={minFee} onChange={handleChange} />
                        </FormControl>
                        <FormControl disabled={disibledCmp}>
                            <InputLabel htmlFor="component-disabled">max fee</InputLabel>
                            <Input id="max_fee" value={maxFee} onChange={handleChange}/>
                        </FormControl>
                        <FormControl disabled={disibledCmp}>
                            <InputLabel htmlFor="component-error">min bet</InputLabel>
                            <Input id="min_bet" value={minBet} onChange={handleChange}/>
                        </FormControl>
                        <FormControl disabled={disibledCmp}>
                            <InputLabel htmlFor="component-simple">max bet</InputLabel>
                            <Input id="max_bet" value={maxBet} onChange={handleChange}/>
                        </FormControl><br/>
                        <FormControl className={classes.formControl} disabled={disibledCmp}>
                            <InputLabel htmlFor="component-helper">Round Times</InputLabel>
                            <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={roundTimes}
                            onChange={handleSelectChange}
                            >
                                <MenuItem value={6}>6 hours</MenuItem>
                                <MenuItem value={7}>7 hours</MenuItem>
                                <MenuItem value={8}>8 hours</MenuItem>
                                <MenuItem value={9}>9 hours</MenuItem>
                                <MenuItem value={10}>10 hours</MenuItem>
                                <MenuItem value={11}>11 hours</MenuItem>
                                <MenuItem value={12}>12 hours</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={3}>
                        <FormControlLabel
                            control={
                                <Switch
                                    checked={state.checkedB}
                                    onChange={handleSwitchChange}
                                    name="checkedB"
                                    color="primary"
                                />
                            }
                            label="Auto"
                        />
                    </Grid>
                    <Grid item xs={2}>
                        <Button variant="contained" color="primary" onClick={loadData}>Load</Button>
                    </Grid>
                    <Grid item xs={2}>
                        <Button variant="contained" color="primary" onClick={updateRoundSetting}>Apply</Button>
                    </Grid>
                    <Grid item xs={2}>
                        <Button variant="contained" onClick={handleClose}>Cancel</Button>
                    </Grid>
                </Grid>
            </DialogContent>
        </Dialog>
    );
}