import React, { Component } from 'react';
import Axios from 'axios';
import AnimatedNumber from "animated-number-react";
import eventBus from "../EventBus";
import  { ethers } from 'ethers';
import revenue from '../assets/img/revenue.png';

class Card extends Component {
  static totRedAmount=0.0;
  static totGreenAmount=0.0;
  static feeAmount=0.0;
  constructor(props) {
    super(props)

    this.state = {
      type:"none",
      clickConnectButton: false,
      totalRedAmount:0.0,
      totalGreenAmount:0.0,
      ethAmount:0.0,
      mainCls: this.props.cardCls
    }
    Card.totRedAmount=this.state.totalRedAmount;
    Card.totGreenAmount=this.state.totalGreenAmount;
  }

  loadCardInfo(){
    Axios({
      method: "POST",
      url: "http://157.90.69.185:5000/api/referral/get-card-data",
      data:{
        wallet_key: String(this.props.walletKey)
      }
    }).then((res)=>{
      // console.log(res);
      if(res.data.length > 0){
        Card.totRedAmount=parseFloat(res.data[0].total_red_amount);
        Card.totGreenAmount=parseFloat(res.data[0].total_green_amount);
        let cmp = Card.totRedAmount - Card.totGreenAmount;
        let payAmount = Math.abs(cmp) + Card.feeAmount;
        let status = parseInt(localStorage.getItem("status"));
        this.setState({
          totalRedAmount:Card.totRedAmount,
          totalGreenAmount:Card.totGreenAmount
        });
        if(status == 0){ // when the game finished
          if(cmp === 0)
            eventBus.dispatch("compare_amount", {data:{curIndex: 0, diffValue: 0}});
          else if(cmp < 0)
            eventBus.dispatch("compare_amount", {data:{curIndex: 3, diffValue: Math.abs(cmp)}});
          else
            eventBus.dispatch("compare_amount", {data:{curIndex: 2, diffValue: cmp}});
        } else {// when the game started
          if(cmp === 0)
            eventBus.dispatch("compare_amount", {data:{curIndex: 1, diffValue: 0}}); 
          else if(cmp < 0)
            eventBus.dispatch("compare_amount", {data:{curIndex: 5, diffValue: Math.abs(cmp), payValue:payAmount}});
          else
            eventBus.dispatch("compare_amount", {data:{curIndex: 4, diffValue: cmp, payValue:payAmount}});
        }
      }
    });
  }

  componentWillMount(){
    this.loadCardInfo();
  }

  formatValue = (value) => value.toFixed(5);

  async betToThisMatch(round,teamId,amount,feeAmount){
    if (window.ethereum) {
      let web3 = window.$web3;
      var gasLimitHex = web3.utils.toHex(300000);       
      let data = window.$myContract.methods.bet(round, teamId, 
                      ethers.utils.parseUnits(Number(amount).toFixed(6).toString(),18), 
                      ethers.utils.parseUnits(Number(feeAmount).toFixed(6).toString(),18)).encodeABI();
      const chainId = await window.ethereum.request({ method: 'eth_chainId' });
      var txdetail = {
          from: window.ethereum.selectedAddress,
          to: window.$bettingContractAddress,
          value: web3.utils.toHex(web3.utils.toWei(amount.toString())),
          gas: gasLimitHex,
          gasPrice: web3.utils.toHex(web3.utils.toWei('10', 'gwei')),
          chainId:chainId,
          data:data
      }
      window.ethereum.request({ method: 'eth_sendTransaction', params: [txdetail] }).then( async (res) => {
        if(res){
          let data={
            amount: amount,
            fee: Card.feeAmount,
            total_red_amount: Card.totRedAmount,
            total_green_amount: Card.totGreenAmount,
            wallet_key: String(this.props.walletKey),
            round_number: round,
            which: this.state.type,
            trans_address: "https://ropsten.etherscan.io/tx/"+String(res)
          }
          this.addCardData(data);
          this.loadCardInfo();
          this.startBetNotifications();
        }
      });
    }
  }

  startBet(){
    let wkey=this.props.walletKey;
    if(wkey==="Connect"|| wkey===null){
      alert("please connect to your wallet.");
      return;
    }
    let amount=parseFloat(this.state.ethAmount);
    let fee = parseFloat(this.props.fee);
    let round = this.props.round;
    let teamId=0;
    if(amount<this.props.minBet || amount>this.props.maxBet) {
      alert("Please type exact amount("+this.props.minBet+"ETH ~ "+this.props.maxBet+"ETH).");
      return;
    }
    if(this.state.type==="red") {
      teamId = 0;
      Card.feeAmount=fee*1.1*Card.totRedAmount/100;
      if(amount <= Card.feeAmount){
        alert("You must enter at least "+Card.feeAmount+" ETH.");
        return;
      }
      Card.totRedAmount+=amount;
    } else {
      teamId = 1;
      Card.feeAmount=fee*1.1*Card.totGreenAmount/100;
      if(amount <= Card.feeAmount){
        alert("You must enter at least "+Card.feeAmount+" ETH.");
        return;
      }
      Card.totGreenAmount+=amount;
    }
    this.betToThisMatch(round,teamId,amount,amount*fee/100);
  }

  startBetNotifications=()=>{
    if(this.state.type==="red")
      this.setState({mainCls:"red_card red_anim"});
    else this.setState({mainCls:"green_card green_anim"});
  }

  endNotification(){
    this.setState({mainCls:this.props.cardCls});
  }

  addCardData(rdata){
    Axios({
      method: "POST",
      url: "http://157.90.69.185:5000/api/referral/add-data",
      data: rdata
    });
    eventBus.dispatch("betted", {info:{round: rdata.round_number}});
  }

  changeEthAmount(evt) {
    this.setState({
      ethAmount: parseFloat(evt.target.value, 10)
    });
  }

  render() {
    let {
      recText, recComment, cardColor, requirement, authorImg, addrText, btnCls, inputCls, type, minBet, maxBet
    } = this.props;
    this.state.type = type;
    return(
      <div className={this.state.mainCls} onAnimationEnd={(evt) => { if (evt.animationName=="red_shadow-wave"||evt.animationName=="green_shadow-wave") this.endNotification() }}>
        <div style={{color:"white", textAlign:"center", marginTop:"15px"}}>
          <span style = {{lineHeight: "60px", verticalAlign:"top"}}>{recText}</span>
          <span style={{marginLeft:"10px"}}><img src={revenue} width="15%" alt="revenue icon"/></span>
        </div>
        <AnimatedNumber className="mark-pan" style={{wordWrap: "break-word",borderColor:{cardColor},transition: '3s ease-out'}}
          value={(this.state.type==="red")?this.state.totalRedAmount:this.state.totalGreenAmount}
          formatValue={this.formatValue}
        />
        <div className="mark-comment-pan">{recComment}</div>
        <div className="require-text">If you want to invest to the red team, send any amount of ethers (minimum: {requirement}) to this address:</div>
        <div>
          <img src={authorImg} width="40%" style={{marginLeft:"5%",marginTop:"5px"}} alt="authentication icon"/>
        </div>
        <div style={{color:"white", fontSize:"12px", textAlign:"center", paddingTop:"25px", wordWrap: "break-word"}}>{addrText}</div>
        <div style={{marginTop:"20px"}}>
          <span style={{marginRight:"10px"}}>Amount:</span>
          <input type="text" className={inputCls} id="fname" placeholder={`${minBet} ETH ~ ${maxBet} ETH`} onChange={this.changeEthAmount.bind(this)}/>
          <button className={btnCls} onClick={this.startBet.bind(this)}>Bet</button>
        </div>
      </div>
    );
  }
}
export default Card;
