import React from 'react'

function TextPan1(props){
    let {color,borderColor,backColor,text,link,ml,dw, fontSize, flt} = props;
    let mml = "";
    mml = (dw < 800)? mml = "5%": ml;
    return(
        <div className = "text-pan-cls" style = {{color: color, borderColor:borderColor, backgroundColor:backColor, marginLeft:mml, fontSize:fontSize, float:flt}} >
            {link === ""? text : <a href={link} style={{color:color}}>{text}</a>}
        </div>
    );
}

export default TextPan1;