import React from 'react';
import {Grid} from '@material-ui/core';
import HistoryPanel from './history-panel'
import HelpNote from '../components/HelpNote';
import eventBus from "../EventBus";
import choiceTeam from '../assets/img/team.png';
import Axios from 'axios';

class PaymentHistory extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            diffValue: 0.0,
            payValue: 0.0,
            curFee: 0.0,
            displayRedNote: "none",
            displayGreenNote: "none",
            green_posts:{},
            red_posts:{}
        };
        this.loadHistory = this.loadHistory.bind(this);   
        this.screenWidth = window.innerWidth;        
    }
    componentDidMount() {
        this.screenWidth = window.innerWidth;
        eventBus.on("compare_amount", (data) =>
            this.evolStatus(data)
        );
        eventBus.on("betted", (data) =>
            this.loadAllHistory(data.info.round)
        );
        let round=parseInt(localStorage.getItem("current_round"));
        this.loadAllHistory(round);

    }
    componentWillUnmount() {
        eventBus.remove("compare_amount");
        eventBus.remove("betted");
    }
    loadAllHistory(round){
        this.loadHistory(round, "red");
        this.loadHistory(round, "green");        
    }

    loadHistory(round, team){
        Axios({
            method: "POST",
            url: "http://157.90.69.185:5000/api/referral/get-team-history",
            data: {
                round: round,
                team: team
            }
        }).then((res)=>{
            try{
                if(res) {
                    if(team=="red"){
                        this.setState({red_posts: res.data});
                    }else {
                        this.setState({green_posts: res.data});
                    }
                }
            }catch(error){
                console.log(error)
            }
        })
    }
    evolStatus(data){
        if(data.data.curIndex===5)
            this.setState({ displayRedNote:"show", displayGreenNote:"none" });
        else if(data.data.curIndex===4)
            this.setState({ displayRedNote:"none", displayGreenNote:"show" });
        else this.setState({ displayRedNote:"none", displayGreenNote:"none" });

        let fee = localStorage.getItem("current_fee");
        if(fee==0.0) fee=0.00000001;
        this.setState({
            diffValue : data.data.diffValue,
            payValue : data.data.payValue,
            curFee : fee
        })
    }
    render() {
        return (
            <div style = {this.screenWidth<800?{marginTop:"15%", textAlign:"center"}:{marginTop:"5%", textAlign:"center"}}>
                <div><img src={choiceTeam} width="60%"/></div>
                <div>
                    <Grid container style={{padding:"0% 10% 10% 10%"}} justify="center">
                        <Grid item xs = {12} sm = {12} md = {4}>
                            <HelpNote payValue={this.state.payValue}
                                      fee={this.state.curFee}
                                      diffValue={this.state.diffValue}
                                      display={this.state.displayRedNote}
                            />
                            <HistoryPanel borderColor="#f93f44" data={this.state.red_posts} which="red"/>
                        </Grid>
                        <Grid item xs = {12} sm = {12} md = {2}></Grid>
                        <Grid item xs = {12} sm = {12} md = {4}>
                            <HelpNote payValue={this.state.payValue}
                                      fee={this.state.curFee}
                                      diffValue={this.state.diffValue}
                                      display={this.state.displayGreenNote}
                            />
                            <HistoryPanel borderColor="#78f93f" data={this.state.green_posts} which="green"/>
                        </Grid>
                    </Grid>
                </div>
            </div>
        )
    }
}

export default PaymentHistory;